import axios from 'axios';
import router from './../router'
import qs from 'qs';
import Vant from 'vant';

export default {
    install: Vue => {
        Vue.use(Vant);
        Vue.prototype.$axios = axios
        // axios.defaults.withCredentials = true
        console.log(
            "%c ",
            'background-image:url("http://guifan.qwangluo.net/logo1.png");background-size:100% 100%;background-repeat:no-repeat;background-position:center center;line-height:30px;padding:9px 39px;'
        );
        console.log('%c JavaScript ',

            'color: white;padding:3px 3px; background-color: #2274A5');
        axios.defaults.timeout = 10000; //请求超时
        const no_login_code = -1; //未登录返回的状态码
        const success_code = 1; //成功返回的状态码
        const err_code = 0; //失败返回的状态码

        if (process.env.NODE_ENV == 'development') setPath('') //设置开发环境域名
        else if (process.env.NODE_ENV == 'production') setPath('') //设置正式生产环境域名
        // else if (process.env.NODE_ENV == 'debug') setPath('') //设置测试环境域名

        function setPath(path) {
            axios.defaults.baseURL = path;
            Vue.prototype.httpPath = path;
        }

        Vue.prototype.$get = params => {
            const token = localStorage.getItem("token") || "";
            axios
                .get(params.url, {
                    params: Object.assign({}, params.data, token ? {
                        token
                    } : "")
                })
                .then(res => {
                    if (res.data.code == no_login_code) {
                        Vue.prototype.$toast('未登录');
                        if (token) localStorage.removeItem("token");
                        setTimeout(() => {
                            router.replace('login')
                        }, 1500);
                    } else if (res.data.code == success_code) {
                        console.log(params.url + ':', res);

                        if (params.success)
                            params.success(res);
                    } 
                 
                    else if (res.data.code == err_code) {
                        if (params.tip){
                           params.tip('val')
                           Vue.prototype.$toast(res.data.msg);
                           console.group('%c' + params.url, 'color:#6600FF;background-image:url("http://guifan.qwangluo.net/tim22.gif");background-size:100% 100%;background-repeat:no-repeat;line-height:30px;padding:10px 0;', res.data)
                           
                       } 
                       // 有轻提示
                       else if (params.tips){
                           params.tips('vals')
                           console.group('%c' + params.url, 'color:#6600FF;background-image:url("http://guifan.qwangluo.net/tim22.gif");background-size:100% 100%;background-repeat:no-repeat;line-height:30px;padding:10px 0;', res.data)
                           
                       }
                       // 无轻提示
                   

                     
                   }
                    
                })
                .catch(err => {
                    console.log('服务器请求失败', err)

                    if (params.fail)
                        params.fail(err);
                })
        }

        Vue.prototype.$post = params => {
            const token = localStorage.getItem("token") || "";
            let $axios;
            if (params.upload) {
                params.data.append('token', token ? token : "")
                let config = {
                    headers: {
                        'Content-Type': 'multipart/form-data'
                    }
                };
                $axios = axios.post(params.url, params.data, config)
            } else if (params.noToken) {
                let data = qs.stringify(Object.assign({}, params.data))
                $axios = axios.post(params.url, data)
            } else {
                let data = qs.stringify(Object.assign({}, params.data, token ? {
                    token
                } : {}))
                $axios = axios.post(params.url, data)
            }

            $axios
                .then(res => {
                    console.log(res)
                    if (res.data.code == no_login_code) {
                        Vue.prototype.$toast('未登录');
                        if (token) localStorage.removeItem("token");
                        setTimeout(() => {
                            router.replace('login')
                        }, 1500);
                    } else if (res.data.code == success_code) {

                        console.group('%c' + params.url, 'color:#6600FF;background-image:url("http://guifan.qwangluo.net/tim22.gif");background-size:100% 100%;background-repeat:no-repeat;line-height:30px;padding:10px 0;', res.data)

                        if (params.success)
                            params.success(res);
                    } else if (res.data.code == err_code) {
                         if (params.tip){
                            params.tip('val')
                            Vue.prototype.$toast(res.data.msg);
                            console.group('%c' + params.url, 'color:#6600FF;background-image:url("http://guifan.qwangluo.net/tim22.gif");background-size:100% 100%;background-repeat:no-repeat;line-height:30px;padding:10px 0;', res.data)
                            
                        } 
                        // 有轻提示
                        else if (params.tips){
                            params.tips('vals')
                            console.group('%c' + params.url, 'color:#6600FF;background-image:url("http://guifan.qwangluo.net/tim22.gif");background-size:100% 100%;background-repeat:no-repeat;line-height:30px;padding:10px 0;', res.data)
                            
                        }
                        // 无轻提示
                    

                      
                    }
                })
                .catch(err => {
                    console.log('服务器请求失败', err)
                    if (params.fail)
                        params.fail(err);
                })
        }

    }
}